@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-400.woff2") format("woff2"), url("../fonts/montserrat-400.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-500.woff2") format("woff2"), url("../fonts/montserrat-500.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/montserrat-700.woff2") format("woff2"), url("../fonts/montserrat-700.woff") format("woff");
}
*, *::before, *::after {
  box-sizing: border-box;
}
*:focus-visible {
  outline: 2px solid;
  outline-offset: 2px;
}
@supports not selector(:focus-visible) {
  *.focus-visible:focus {
    outline: 2px solid;
    outline-offset: 2px;
  }
}

html {
  min-width: 320px;
  scroll-behavior: smooth;
  font-size: 16px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  color: #011c40;
}

body {
  margin: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]),
button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  color: inherit;
  opacity: 0.7;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

textarea {
  resize: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}
input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}
input::-webkit-search-decoration, input::-webkit-search-cancel-button, input::-webkit-search-results-button, input::-webkit-search-results-decoration {
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}
input[type=number]::-webkit-outer-spin-button, input[type=number]::-webkit-inner-spin-button {
  appearance: none;
}

:disabled {
  opacity: 0.5;
  pointer-events: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

@media (max-width: 767px) {
  .mobile-hide {
    display: none;
  }
}

@media (min-width: 768px) {
  .mobile-show {
    display: none;
  }
}

.container {
  width: 100%;
  max-width: 1250px;
  margin-right: auto;
  margin-left: auto;
  padding: 0 45px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  padding: 9px 20px 10px;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.5;
  font-family: "Montserrat", "Arial", sans-serif;
  text-align: center;
  overflow-wrap: anywhere;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  border-radius: 0;
  cursor: pointer;
  user-select: none;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .btn:hover, .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .btn.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .btn:active, .btn:hover:active, .btn.focus-visible:active {
    opacity: 0.5;
  }
}

.firstscreen {
  position: relative;
  color: #f9fbfd;
  background-color: #011c40;
  z-index: -2;
}

.firstscreen__promo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 542px;
  padding: 34px 95px 150px;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  transition: opacity 0.3s ease;
}
@media (max-width: 1023px) {
  .firstscreen__promo {
    min-height: 560px;
    padding: 64px 0 150px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo {
    min-height: 437px;
    padding: 30px 0 96px;
  }
}
.firstscreen__promo h1 {
  margin: 0 0 12px;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
}
@media (max-width: 1023px) {
  .firstscreen__promo h1 {
    font-size: 50px;
    line-height: 61px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo h1 {
    font-size: 36px;
    line-height: 45px;
  }
}
.firstscreen__promo p {
  margin: 0;
  font-size: 20px;
  line-height: 24px;
}
@media (max-width: 1023px) {
  .firstscreen__promo p {
    font-size: 18px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .firstscreen__promo p {
    font-size: 16px;
    margin-right: 58px;
  }
}

.firstscreen .is-active + .firstscreen__promo,
.firstscreen .no-js:focus-within + .firstscreen__promo,
.firstscreen .no-js:hover + .firstscreen__promo {
  margin-top: 63px;
  opacity: 0;
}

.firstscreen__image {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.logo {
  transition: opacity 0.3s ease;
  --color: currentColor;
  width: 70px;
}
@media (hover: hover) {
  .logo:hover, .logo:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.logo:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .logo.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .logo:active, .logo:hover:active, .logo.focus-visible:active {
    opacity: 0.5;
  }
}
@media (max-width: 1023px) {
  .logo {
    width: 38px;
  }
}
@media (max-width: 767px) {
  .logo {
    width: 42px;
  }
}
.logo:not([href]) {
  pointer-events: none;
}
.logo svg {
  display: block;
  width: 100%;
  height: auto;
}

.nav {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .nav {
    flex-direction: column;
  }
}

.nav__link {
  display: block;
  margin: 0 0 0 34px;
  padding: 10px 0;
  font-weight: 500;
  line-height: 20px;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .nav__link:hover, .nav__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.nav__link:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .nav__link.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .nav__link:active, .nav__link:hover:active, .nav__link.focus-visible:active {
    opacity: 0.5;
  }
}
@media (max-width: 1023px) {
  .nav__link {
    margin-left: 24px;
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .nav__link {
    margin: 0 0 8px 10px;
    font-size: 18px;
    line-height: 22px;
  }
}

.page {
  min-height: 100vh;
}

.page__firstscreen {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .page__firstscreen {
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .page__firstscreen {
    box-shadow: none;
  }
}

.page-header {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  align-content: flex-start;
  padding: 34px 0;
}
@media (max-width: 1023px) {
  .page-header {
    padding: 21px 0 20px;
  }
}
@media (max-width: 767px) {
  .page-header {
    right: -15px;
    left: -15px;
    width: calc(100% + 30px);
    padding: 20px 15px;
  }
  .page-header::before {
    position: absolute;
    top: 0;
    right: 100%;
    bottom: 0;
    width: 100vw;
    background-color: rgba(0, 43, 102, 0.5);
    pointer-events: none;
  }
}

.page-header__toggler {
  display: none;
  padding: 0;
  color: inherit;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .page-header__toggler:hover, .page-header__toggler:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.page-header__toggler:focus-visible {
  opacity: 0.8;
}
@supports not selector(:focus-visible) {
  .page-header__toggler.focus-visible:focus {
    opacity: 0.8;
  }
}
@media (hover: hover) {
  .page-header__toggler:active, .page-header__toggler:hover:active, .page-header__toggler.focus-visible:active {
    opacity: 0.5;
  }
}
.page-header__toggler::before {
  content: "";
  width: 25px;
  height: 22px;
  background-color: currentColor;
  mask: url("../img/sprite.svg#icon-burger") no-repeat center/100%;
}
@media (max-width: 767px) {
  .page-header__toggler {
    display: flex;
  }
}

@media (max-width: 767px) {
  .page-header__nav {
    display: none;
    width: 100%;
    margin-top: 32px;
  }
}

.page-header__nav-list {
  justify-content: flex-end;
  margin: 0;
}

@media (max-width: 767px) {
  .page-header.is-active::before {
    content: "";
  }
}

.page-header.no-js:hover .page-header__toggler,
.page-header.no-js:focus-within .page-header__toggler {
  display: none;
}

@media (max-width: 767px) {
  .page-header.no-js:hover,
.page-header.no-js:focus-within,
.page-header.is-active {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: auto;
    z-index: 1;
    align-items: flex-start;
    width: 285px;
    color: #011c40;
    background-color: #f9fbfd;
    transition: color 0.3s ease, background-color 0.3s ease;
  }
  .page-header.no-js:hover .page-header__toggler::before,
.page-header.no-js:focus-within .page-header__toggler::before,
.page-header.is-active .page-header__toggler::before {
    width: 13px;
    margin-left: auto;
    mask: url("../img/sprite.svg#icon-close") no-repeat center/100%;
  }
  .page-header.no-js:hover .page-header__nav,
.page-header.no-js:focus-within .page-header__nav,
.page-header.is-active .page-header__nav {
    display: block;
  }
}

.content {
  padding: 120px 0;
}
@media (max-width: 1023px) {
  .content {
    padding: 100px 0;
  }
}
@media (max-width: 767px) {
