  margin-bottom: 25px;
}
@media (max-width: 1023px) {
  .content__inner {
    grid-template-columns: 1fr 324px;
    align-items: start;
    gap: 25px;
    margin-bottom: -5px;
    padding-right: 50px;
  }
}
@media (max-width: 767px) {
  .content__inner {
    grid-template-columns: 1fr;
    gap: 43px;
    margin-bottom: 20px;
  }
}
.content__inner h2 {
  margin-top: 12px;
  font-weight: 600;
  font-size: 28px;
}
@media (max-width: 1023px) {
  .content__inner h2 {
    margin: -9px 22px 15px 0;
    font-size: 26px;
    line-height: 36px;
  }
}
@media (max-width: 767px) {
  .content__inner h2 {
    margin: -8px 12px 15px 0;
    font-size: 24px;
    line-height: 34px;
  }
}
@media (max-width: 1023px) {
  .content__inner p {
    margin: 0;
  }
